'use client';

import React from 'react';
import { motion } from 'framer-motion';
import { Search, X, Clock, Pin, Tag, FolderOpen, ChevronDown } from 'lucide-react';
import { useEffect, useState } from 'react';
import { useRouter, useSearchParams } from 'next/navigation';
import api from '@/lib/api';
import { getApiBaseUrl } from '@/lib/utils';
import { getSettings, getPageText } from '@/lib/api';
import Copyright from '@/components/Copyright';
import Masonry from 'react-masonry-css';
import SEOMeta from '@/components/SEOMeta';
import PageBackground from '@/components/PageBackground';

const API_BASE_URL = getApiBaseUrl();

interface Note {
  id: number;
  title: string;
  content: string;
  summary?: string;
  is_published: boolean;
  is_pinned?: boolean;
  published_at?: string;
  created_at: string;
  media_type?: 'none' | 'image' | 'video' | 'music';
  media_urls?: string[];
  external_link?: string;
  category?: string;
  category_id?: number;
  tags?: string;
  password?: string;
  requiresPassword?: boolean;
  cover_image?: string;
  Tags?: Array<{
    id: number;
    name: string;
    color?: string;
  }>;
}

interface NoteCategory {
  id: number;
  name: string;
  type?: string;
  description?: string;
  icon?: string;
  sort_order?: number;
  parent_id?: number;
  created_at?: string;
  updated_at?: string;
}

const formatDateTime = (dateString: string) => {
  const date = new Date(dateString);
  const year = date.getFullYear();
  const month = String(date.getMonth() + 1).padStart(2, '0');
  const day = String(date.getDate()).padStart(2, '0');
  const hours = String(date.getHours()).padStart(2, '0');
  const minutes = String(date.getMinutes()).padStart(2, '0');

  return `${year}-${month}-${day} ${hours}:${minutes}`;
};

const truncate = (str: string, length: number) => {
  if (str.length <= length) return str;
  return str.substring(0, length) + '...';
};

// 过滤markdown符号，返回纯文本内容
const stripMarkdown = (markdown: string): string => {
  if (!markdown) return '';

  let text = markdown;

  // 移除图片语法
  text = text.replace(/!\[([^\]]*)\]\(([^)]+)\)/g, '');
  text = text.replace(/!\[([^\]]*)\]\[([^\]]+)\]/g, '');
  text = text.replace(/!\[([^\]]+)\]/g, '');
  text = text.replace(/!\s*\[/g, '');

  // 移除代码块
  text = text.replace(/```[\s\S]*?```/g, '');
  text = text.replace(/`([^`]+)`/g, '$1');
  text = text.replace(/`/g, '');

  // 移除标题符号
  text = text.replace(/^#{1,6}\s+/gm, '');

  // 移除粗体、斜体、删除线
  text = text.replace(/\*\*([^\*]+)\*\*/g, '$1');
  text = text.replace(/__([^_]+)__/g, '$1');
  text = text.replace(/\*([^\*]+)\*/g, '$1');
  text = text.replace(/_([^_]+)_/g, '$1');
  text = text.replace(/~~([^~]+)~~/g, '$1');

  // 移除列表符号
  text = text.replace(/^[\s]*[-*+]\s+/gm, '');
  text = text.replace(/^[\s]*\d+\.\s+/gm, '');

  // 移除引用符号
  text = text.replace(/^>\s+/gm, '');

  // 移除链接符号
  text = text.replace(/\[([^\]]+)\]\(([^)]+)\)/g, '$1');

  // 移除表格和分割线
  text = text.replace(/\|.*\|/g, '');
  text = text.replace(/^\s*[-*_]{3,}\s*$/gm, '');

  // 清理多余的空格和换行
  text = text
    .replace(/\s+/g, ' ')
    .replace(/\n{3,}/g, '\n\n')
    .replace(/\n\s*\n/g, '\n\n')
    .trim();

  return text;
};

export default function NotesPage() {
  const router = useRouter();
  const searchParams = useSearchParams();
  const [notes, setNotes] = useState<Note[]>([]);
  const [categories, setCategories] = useState<NoteCategory[]>([]);
  const [tags, setTags] = useState<string[]>([]);
  const [selectedCategory, setSelectedCategory] = useState<string>('all');
  const [selectedTag, setSelectedTag] = useState<string>('all');
  const [categoryExpanded, setCategoryExpanded] = useState(false);
  const [tagExpanded, setTagExpanded] = useState(false);
  const [loading, setLoading] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const [searchExpanded, setSearchExpanded] = useState(false);
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);


  // 检测屏幕尺寸
  const [isDesktop, setIsDesktop] = useState(false);

  useEffect(() => {
    const checkScreenSize = () => {
      setIsDesktop(window.innerWidth >= 768);
    };

    checkScreenSize();
    window.addEventListener('resize', checkScreenSize);

    return () => window.removeEventListener('resize', checkScreenSize);
  }, []);

  // 默认设置（避免加载动画）
  const defaultSettings = {
    themeType: 'default',
    footerCopyright: `© ${new Date().getFullYear()} 个人主页. All rights reserved.`,
    noteLayoutColumns: '1',
    showNoteCover: 'true',
    defaultNoteCover: '',
    avatarShape: 'circle',
    showTopNavbar: 'true',
    showWapSidebar: 'true',
    showNoteTags: 'true',
    showNoteCategories: 'true'
  }
  const [settings, setSettings] = useState<any>(defaultSettings);
  // 页面文本配置 - 初始值为空，避免覆盖 layout 中设置的标题
  const [pageText, setPageText] = useState({
    title: '',
    description: '',
    browserTitle: '',
    browserSubtitle: ''
  });
  const [pageTextLoaded, setPageTextLoaded] = useState(false);

  // 从 URL 参数初始化筛选条件
  useEffect(() => {
    const categoryParam = searchParams.get('category');
    const tagParam = searchParams.get('tag');

    if (categoryParam) {
      setSelectedCategory(categoryParam);
    }
    if (tagParam) {
      setSelectedTag(tagParam);
    }
  }, [searchParams]);

  useEffect(() => {
    fetchNotes();
    fetchCategories();
    fetchTags();
    loadSettings();
  }, [page, selectedCategory, selectedTag]);

  // 设置页面标题（仅使用 browserTitle，不受网站标题、副标题影响）
  useEffect(() => {
    // 只有在 browserTitle 有值时才设置标题，避免覆盖 layout 中设置的标题
    if (!pageText.browserTitle) return;

    let title = pageText.browserTitle;

    if (selectedCategory !== 'all') {
      title = `${selectedCategory} - ${pageText.browserTitle}`;
    } else if (selectedTag !== 'all') {
      title = `${selectedTag} - ${pageText.browserTitle}`;
    }

    // 添加副标题
    if (pageText.browserSubtitle) {
      title = `${title} - ${pageText.browserSubtitle}`;
    }

    document.title = title;
  }, [selectedCategory, selectedTag, pageText.browserTitle, pageText.browserSubtitle]);

  const loadSettings = async () => {
    try {
      // 并行加载设置和页面文本配置
      const [settingsResponse, pageTextResponse] = await Promise.all([
        getSettings(),
        getPageText('notes')
      ]);

      if (settingsResponse.success && settingsResponse.data) {
        const settingsObj: any = {};
        settingsResponse.data.forEach((setting: any) => {
          settingsObj[setting.key] = setting.value;
        });
        setSettings({
          themeType: settingsObj.themeType || 'default',
          footerCopyright: settingsObj.footerCopyright || `© ${new Date().getFullYear()} 个人主页. All rights reserved.`,
          noteLayoutColumns: settingsObj.noteLayoutColumns || '1',
          showNoteCover: settingsObj.showNoteCover || 'true',
          defaultNoteCover: settingsObj.defaultNoteCover || '',
          avatarShape: settingsObj.avatarShape || 'circle',
          showTopNavbar: settingsObj.showTopNavbar || 'true',
          showWapSidebar: settingsObj.showWapSidebar || 'true',
          // 如果配置存在则使用配置值，否则默认显示
          showNoteTags: settingsObj.showNoteTags ? settingsObj.showNoteTags === 'true' : true,
          showNoteCategories: settingsObj.showNoteCategories ? settingsObj.showNoteCategories === 'true' : true
        });
      }

      // 从新的 page_texts 表加载页面文本配置
      if (pageTextResponse.success && pageTextResponse.data) {
        setPageText({
          title: pageTextResponse.data.title || '全部笔记',
          description: pageTextResponse.data.description || '探索所有已发布的笔记内容',
          browserTitle: pageTextResponse.data.browserTitle || '全部笔记',
          browserSubtitle: pageTextResponse.data.browserSubtitle || ''
        });
        setPageTextLoaded(true);
      }
    } catch (error) {
      console.error('Failed to load settings:', error);
      setPageTextLoaded(true); // 即使失败也标记为已加载，使用默认值
    }
  };

  const fetchNotes = async () => {
    try {
      const params: any = {
        is_published: true,
        page,
        limit: 12,
      };

      // 添加筛选参数
      if (selectedCategory !== 'all') {
        params.category = selectedCategory;
      }
      if (selectedTag !== 'all') {
        params.tag = selectedTag;
      }

      const response = await api.get('/notes', { params });
      const data = response.data?.data;
      const notesArray = Array.isArray(data?.notes) ? data.notes : [];
      setNotes(notesArray);
      setTotalPages(data?.pagination?.totalPages || 1);
    } catch (error) {
      console.error('Failed to fetch notes:', error);
      setNotes([]);
    } finally {
      setLoading(false);
    }
  };

  const fetchCategories = async () => {
    try {
      const response = await api.get('/notes/categories/list');
      const data = response.data?.data;
      setCategories(Array.isArray(data) ? data : []);
    } catch (error) {
      console.error('Failed to fetch categories:', error);
      setCategories([]);
    }
  };

  const fetchTags = async () => {
    try {
      const response = await api.get('/notes/tags/stats');
      const data = response.data?.data;
      setTags(Array.isArray(data) ? data.map((tag: any) => tag.name) : []);
    } catch (error) {
      console.error('Failed to fetch tags:', error);
      setTags([]);
    }
  };

  const handleNoteClick = (note: Note) => {
    // 直接跳转到独立页面
    router.push(`/note/${note.id}`);
  };

  const handleSearch = (e: React.FormEvent) => {
    e.preventDefault();
  };

  const handleClearSearch = () => {
    setSearchQuery('');
  };

  // 基于搜索查询过滤笔记
  const filteredNotes = notes.filter((note) => {
    if (!searchQuery.trim()) return true;

    const query = searchQuery.toLowerCase();
    const titleMatch = note.title?.toLowerCase().includes(query);
    const contentMatch = stripMarkdown(note.content).toLowerCase().includes(query);
    const categoryMatch = note.category?.toLowerCase().includes(query);
    const tagsMatch = note.Tags?.some(tag => tag.name.toLowerCase().includes(query)) ||
                     note.tags?.toLowerCase().includes(query);

    return titleMatch || contentMatch || categoryMatch || tagsMatch;
  });

  // 创建分类名称到分类对象的映射
  const categoryMap = new Map(categories.map(cat => [cat.name, cat]));

  // 布局规则辅助函数
  const columns = parseInt(settings?.noteLayoutColumns) || 1;

  // 是否使用横向布局（封面在左侧）
  const useHorizontalLayout = () => {
    if (columns === 1) return true; // 1列：所有设备横向
    if (columns === 2 || columns === 3) return isDesktop; // 2-3列：电脑端横向，手机端纵向
    return false; // 4列：所有设备纵向
  };

  // 是否显示标签和分类
  const shouldShowTagsAndCategories = () => {
    if (!isDesktop && columns >= 2) return false; // 手机端2列及以上不显示
    if (isDesktop && columns === 4) return false; // 电脑端4列不显示
    return true;
  };

  // 是否显示摘要
  const shouldShowSummary = () => {
    if (!isDesktop && columns >= 2) return false; // 手机端2列不显示
    if (isDesktop && columns === 4) return false; // 电脑端4列不显示
    return true;
  };

  const isHorizontal = useHorizontalLayout();
  const showTagsAndCats = shouldShowTagsAndCategories() && (settings?.showNoteTags || settings?.showNoteCategories);
  const showSummary = shouldShowSummary();

  const isBlogTheme = settings?.themeType === 'blog';
  const showTopNavbar = settings?.showTopNavbar !== 'false';
  const showWapSidebar = settings?.showWapSidebar !== 'false';

  // 显示搜索结果的提示
  const showSearchResults = searchQuery.trim() && filteredNotes.length > 0;
  const showNoResults = searchQuery.trim() && filteredNotes.length === 0;

  return (
    <main className="min-h-screen bg-bg-primary relative flex flex-col hide-scrollbar">
      <SEOMeta />
      {/* 背景图片 */}
      <PageBackground />

      {/* 内容区域 */}
      <div className="relative z-0 flex-1 flex flex-col">
        {/* 给导航栏留出空间 */}
        <div className="h-16"></div>

        <div className="container mx-auto px-4 max-w-6xl py-8">
          {/* 页面标题 */}
          <div className="text-center mb-8">
            <h1 className="text-3xl font-bold text-text-primary mb-2">{pageText.title}</h1>
            <p className="text-text-secondary">{pageText.description}</p>
          </div>

          {/* 筛选器和搜索框 */}
          <div className="mb-6">
            {/* 电脑端：分类、标签、搜索框在一排 */}
            <div className="flex flex-col md:flex-row gap-3 mb-3">
              {/* 分类筛选 */}
              {categories.length > 0 && (
                <div className="relative md:w-48 flex-shrink-0">
                  <button
                    onClick={() => { setCategoryExpanded(!categoryExpanded); setTagExpanded(false); }}
                    className="w-full flex items-center justify-between px-4 py-3 bg-bg-secondary rounded-lg border border-border-primary hover:bg-bg-tertiary transition-all"
                  >
                    <div className="flex items-center gap-2">
                      <FolderOpen className="w-4 h-4 text-text-secondary" />
                      <span className="text-sm font-medium text-text-primary truncate">
                        {selectedCategory === 'all' ? '全部分类' : selectedCategory}
                      </span>
                    </div>
                    <ChevronDown className={`w-4 h-4 text-text-tertiary transition-transform flex-shrink-0 ${categoryExpanded ? 'rotate-180' : ''}`} />
                  </button>
                  {categoryExpanded && (
                    <div className="absolute top-full left-0 right-0 mt-2 p-3 bg-bg-secondary rounded-lg border border-border-primary shadow-lg z-20">
                      <div className="flex gap-2 flex-wrap">
                        <button
                          onClick={() => { setSelectedCategory('all'); setPage(1); setCategoryExpanded(false); }}
                          className={`px-4 py-2 rounded-lg text-sm font-medium transition-all ${
                            selectedCategory === 'all'
                              ? 'bg-primary-500 text-white shadow-md'
                              : 'bg-bg-tertiary text-text-secondary hover:bg-bg-primary border border-border-primary'
                          }`}
                        >
                          全部
                        </button>
                        {categories.map((category) => (
                          <button
                            key={category.id || category.name}
                            onClick={() => { setSelectedCategory(category.name); setPage(1); setCategoryExpanded(false); }}
                            className={`px-4 py-2 rounded-lg text-sm font-medium transition-all ${
                              selectedCategory === category.name
                                ? 'bg-primary-500 text-white shadow-md'
                                : 'bg-bg-tertiary text-text-secondary hover:bg-bg-primary border border-border-primary'
                            }`}
                          >
                            {category.icon && <span className="mr-1.5">{category.icon}</span>}
                            {category.name}
                          </button>
                        ))}
                      </div>
                    </div>
                  )}
                </div>
              )}

              {/* 标签筛选 */}
              {tags.length > 0 && (
                <div className="relative md:w-48 flex-shrink-0">
                  <button
                    onClick={() => { setTagExpanded(!tagExpanded); setCategoryExpanded(false); }}
                    className="w-full flex items-center justify-between px-4 py-3 bg-bg-secondary rounded-lg border border-border-primary hover:bg-bg-tertiary transition-all"
                  >
                    <div className="flex items-center gap-2">
                      <Tag className="w-4 h-4 text-text-secondary" />
                      <span className="text-sm font-medium text-text-primary truncate">
                        {selectedTag === 'all' ? '全部标签' : selectedTag}
                      </span>
                    </div>
                    <ChevronDown className={`w-4 h-4 text-text-tertiary transition-transform flex-shrink-0 ${tagExpanded ? 'rotate-180' : ''}`} />
                  </button>
                  {tagExpanded && (
                    <div className="absolute top-full left-0 right-0 mt-2 p-3 bg-bg-secondary rounded-lg border border-border-primary shadow-lg z-20">
                      <div className="flex gap-2 flex-wrap">
                        <button
                          onClick={() => { setSelectedTag('all'); setPage(1); setTagExpanded(false); }}
                          className={`px-4 py-2 rounded-lg text-sm font-medium transition-all ${
                            selectedTag === 'all'
                              ? 'bg-primary-500 text-white shadow-md'
                              : 'bg-bg-tertiary text-text-secondary hover:bg-bg-primary border border-border-primary'
                          }`}
                        >
                          全部
                        </button>
                        {tags.slice(0, 10).map((tag) => (
                          <button
                            key={tag}
                            onClick={() => { setSelectedTag(tag); setPage(1); setTagExpanded(false); }}
                            className={`px-4 py-2 rounded-lg text-sm font-medium transition-all ${
                              selectedTag === tag
                                ? 'bg-primary-500 text-white shadow-md'
                                : 'bg-bg-tertiary text-text-secondary hover:bg-bg-primary border border-border-primary'
                            }`}
                          >
                            {tag}
                          </button>
                        ))}
                      </div>
                    </div>
                  )}
                </div>
              )}

              {/* 电脑端搜索按钮/搜索框 */}
              <div className="hidden md:flex items-center">
                {searchExpanded ? (
                  <div className="w-64">
                    <div className="relative flex items-center">
                      <Search className="absolute left-3 top-0 bottom-0 my-auto w-4 h-4 text-text-tertiary pointer-events-none" />
                      <input
                        type="text"
                        placeholder="搜索笔记..."
                        value={searchQuery}
                        onChange={(e) => setSearchQuery(e.target.value)}
                        className="w-full h-[46px] pl-9 pr-9 bg-bg-secondary border border-border-primary rounded-lg text-sm text-text-primary placeholder-text-tertiary focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent transition-all"
                        autoComplete="off"
                        autoFocus
                      />
                      <button
                        type="button"
                        onClick={() => { setSearchQuery(''); setSearchExpanded(false); }}
                        className="absolute right-3 top-0 bottom-0 my-auto w-5 h-5 flex items-center justify-center text-text-tertiary hover:text-text-primary transition-colors"
                        aria-label="关闭搜索"
                      >
                        <X className="w-4 h-4" />
                      </button>
                    </div>
                  </div>
                ) : (
                  <button
                    type="button"
                    onClick={() => setSearchExpanded(true)}
                    className="flex items-center justify-center w-[46px] h-[46px] bg-bg-secondary border border-border-primary rounded-lg text-text-secondary hover:bg-bg-tertiary hover:text-text-primary transition-all"
                    aria-label="搜索"
                  >
                    <Search className="w-5 h-5" />
                  </button>
                )}
              </div>
            </div>

            {/* 搜索结果显示 */}
            {showSearchResults && (
              <div className="p-4 rounded-xl border" style={{ backgroundColor: 'var(--info-bg)', borderColor: 'var(--info-border)' }}>
                <div className="flex items-center justify-center gap-2">
                  <span className="text-sm" style={{ color: 'var(--info-text)' }}>
                    搜索结果: 找到 {filteredNotes.length} 条相关笔记
                    <span className="font-medium ml-1">"{searchQuery}"</span>
                  </span>
                </div>
              </div>
            )}

            {/* 无搜索结果提示 */}
            {showNoResults && (
              <div className="p-4 bg-bg-tertiary rounded-xl border border-border-primary text-center">
                <span className="text-sm text-text-tertiary">
                  没有找到包含 "{searchQuery}" 的笔记
                </span>
              </div>
            )}
          </div>

          {/* 笔记列表 */}
          {filteredNotes.length > 0 ? (
            <Masonry
              breakpointCols={{
                default: parseInt(settings?.noteLayoutColumns) || 1,
                1536: parseInt(settings?.noteLayoutColumns) || 1,
                1280: parseInt(settings?.noteLayoutColumns) === 4 ? 3 : parseInt(settings?.noteLayoutColumns) || 1,
                1024: parseInt(settings?.noteLayoutColumns) >= 3 ? 2 : parseInt(settings?.noteLayoutColumns) || 1,
                768: parseInt(settings?.noteLayoutColumns) > 1 ? 2 : 1,
                640: parseInt(settings?.noteLayoutColumns) > 1 ? 2 : 1
              }}
              className="flex -ml-3 w-auto"
              columnClassName="pl-3 bg-clip-padding"
            >
              {filteredNotes.map((note, index) => {
                const coverImageUrl = (note.cover_image || settings?.defaultNoteCover || '').startsWith('http')
                  ? (note.cover_image || settings?.defaultNoteCover)
                  : `${API_BASE_URL}${note.cover_image || settings?.defaultNoteCover}`;

                return (
                  <motion.div
                    key={note.id}
                    onClick={() => handleNoteClick(note)}
                    className="group block rounded-lg shadow-sm hover:shadow-lg hover:-translate-y-0.5 border bg-bg-secondary border-border-primary transition-all duration-300 cursor-pointer overflow-hidden mb-3"
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ delay: index * 0.05 }}
                  >
                    {/* 横向布局（封面在左侧） - 1列 或 2-3列的电脑端 */}
                    {isHorizontal ? (
                      <div className="flex gap-3 items-stretch p-3">
                        {/* 左侧封面图 - 1:1正方形（仅当有封面图时显示） */}
                        {(note.cover_image || settings?.defaultNoteCover) && settings?.showNoteCover === 'true' && (
                          <div className="flex-shrink-0 w-24 h-24 rounded-md overflow-hidden bg-bg-tertiary">
                            <img
                              src={coverImageUrl}
                              alt={note.title || '封面图'}
                              className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
                              onError={(e) => {
                                const target = e.target as HTMLImageElement;
                                target.style.display = 'none';
                              }}
                            />
                          </div>
                        )}

                        {/* 右侧内容 */}
                        <div className="flex-1 flex flex-col justify-center min-w-0">
                          {/* 标题和置顶标签 */}
                          <div className="flex items-start gap-2 mb-1">
                            {note.title && (
                              <h3 className="text-base font-semibold text-text-primary line-clamp-1 flex-1 transition-colors">
                                {note.title}
                              </h3>
                            )}
                            {note.is_pinned && (
                              <div className="flex-shrink-0 px-1.5 py-0.5 rounded text-[10px] font-semibold flex items-center gap-0.5" style={{ background: 'linear-gradient(to right, var(--pinned-gradient-from), var(--pinned-gradient-to))', color: 'var(--pinned-text)' }}>
                                <Pin className="w-2.5 h-2.5 fill-current" />
                              </div>
                            )}
                          </div>

                          {/* 摘要/内容预览 */}
                          {showSummary && (
                            <p className="text-xs text-text-secondary line-clamp-2 mb-2 leading-relaxed">
                              {note.summary || (note.content ? truncate(stripMarkdown(note.content), 80) : '暂无内容')}
                            </p>
                          )}

                          {/* 底部：时间、分类、标签 */}
                          <div className="flex items-center gap-2 flex-wrap text-[10px]">
                            <div className="flex items-center gap-1 text-text-tertiary">
                              <Clock className="w-2.5 h-2.5" />
                              <span>{formatDateTime(note.published_at || note.created_at)}</span>
                            </div>

                            {showTagsAndCats && settings?.showNoteCategories && note.category && (
                              <span
                                className="px-1.5 py-0.5 bg-primary-100 text-primary-700 rounded-full font-medium cursor-pointer hover:bg-primary-200 transition-colors"
                                onClick={(e) => {
                                  e.stopPropagation();
                                  setSelectedCategory(note.category!);
                                  setPage(1);
                                }}
                              >
                                {categoryMap.get(note.category)?.icon && (
                                  <span className="mr-0.5">{categoryMap.get(note.category)?.icon}</span>
                                )}
                                {note.category}
                              </span>
                            )}

                            {showTagsAndCats && settings?.showNoteTags && note.Tags && note.Tags.length > 0 && (
                              <>
                                {note.Tags.slice(0, 2).map((tag) => (
                                  <span
                                    key={tag.id}
                                    className="px-1.5 py-0.5 rounded-full font-medium cursor-pointer transition-opacity"
                                    style={tag.color ? { backgroundColor: `${tag.color}20`, color: tag.color } : { backgroundColor: 'var(--tag-bg)', color: 'var(--tag-text)' }}
                                    onMouseEnter={(e) => {
                                      if (!tag.color) {
                                        e.currentTarget.style.backgroundColor = 'var(--tag-hover-bg)';
                                      }
                                    }}
                                    onMouseLeave={(e) => {
                                      if (!tag.color) {
                                        e.currentTarget.style.backgroundColor = 'var(--tag-bg)';
                                      }
                                    }}
                                    onClick={(e) => {
                                      e.stopPropagation();
                                      setSelectedTag(tag.name);
                                      setPage(1);
                                    }}
                                  >
                                    {tag.name}
                                  </span>
                                ))}
                                {note.Tags.length > 2 && (
                                  <span className="px-1.5 py-0.5 rounded-full text-[10px]" style={{ backgroundColor: 'var(--tag-bg)', color: 'var(--tag-text)' }}>
                                    +{note.Tags.length - 2}
                                  </span>
                                )}
                              </>
                            )}

                            {/* 旧格式标签（字符串） */}
                            {showTagsAndCats && settings?.showNoteTags && (!note.Tags || note.Tags.length === 0) && note.tags && (
                              <>
                                {note.tags.split(',').slice(0, 2).map((tag, idx) => (
                                  <span
                                    key={idx}
                                    className="px-1.5 py-0.5 rounded-full font-medium cursor-pointer transition-opacity"
                                    style={{ backgroundColor: 'var(--tag-bg)', color: 'var(--tag-text)' }}
                                    onClick={(e) => {
                                      e.stopPropagation();
                                      setSelectedTag(tag.trim());
                                      setPage(1);
                                    }}
                                  >
                                    {tag.trim()}
                                  </span>
                                ))}
                                {note.tags.split(',').length > 2 && (
                                  <span className="px-1.5 py-0.5 rounded-full text-[10px]" style={{ backgroundColor: 'var(--tag-bg)', color: 'var(--tag-text)' }}>
                                    +{note.tags.split(',').length - 2}
                                  </span>
                                )}
                              </>
                            )}
                          </div>
                        </div>
                      </div>
                    ) : (
                      /* 纵向布局（封面在上方） - 4列 或 2-3列的手机端 */
                      <div className="flex flex-col">
                        {/* 封面图 - 1:1正方形 */}
                        {(note.cover_image || settings?.defaultNoteCover) && settings?.showNoteCover === 'true' && (
                          <div className="relative w-full pt-[100%] overflow-hidden bg-gradient-to-br from-primary-100 to-primary-200">
                            <img
                              src={coverImageUrl}
                              alt={note.title || '封面图'}
                              className="absolute inset-0 w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
                              onError={(e) => {
                                const target = e.target as HTMLImageElement;
                                target.style.display = 'none';
                              }}
                            />

                            {/* 置顶标识 */}
                            {note.is_pinned && (
                              <div className="absolute top-2 right-2 bg-bg-secondary/90 backdrop-blur-sm rounded-full px-2 py-1 text-[10px] text-text-primary font-semibold flex items-center gap-1 border border-border-primary/50">
                                <Pin className="w-2.5 h-2.5 fill-current" />
                                <span>置顶</span>
                              </div>
                            )}
                          </div>
                        )}

                        {/* 标题和描述 */}
                        <div className="p-3">
                          {note.title && (
                            <h3 className="font-semibold text-base text-text-primary transition-colors line-clamp-2 mb-1">
                              {note.title}
                            </h3>
                          )}

                          {/* 摘要 */}
                          {showSummary && (
                            <p className="text-xs text-text-secondary line-clamp-2 leading-relaxed mb-2">
                              {note.summary || (note.content ? truncate(stripMarkdown(note.content), 100) : '暂无内容')}
                            </p>
                          )}

                          {/* 底部信息 */}
                          <div className="flex items-center justify-between gap-2 text-[10px]">
                            <div className="flex items-center gap-1 text-text-tertiary">
                              <Clock className="w-2.5 h-2.5" />
                              <span>{formatDateTime(note.published_at || note.created_at)}</span>
                            </div>

                            {showTagsAndCats && (
                              <div className="flex items-center gap-1 flex-wrap">
                                {settings?.showNoteCategories && note.category && (
                                  <span
                                    className="px-1.5 py-0.5 bg-primary-100 text-primary-700 rounded-full font-medium cursor-pointer hover:bg-primary-200 transition-colors"
                                    onClick={(e) => {
                                      e.stopPropagation();
                                      setSelectedCategory(note.category!);
                                      setPage(1);
                                    }}
                                  >
                                    {categoryMap.get(note.category)?.icon && (
                                      <span className="mr-0.5">{categoryMap.get(note.category)?.icon}</span>
                                    )}
                                    {note.category}
                                  </span>
                                )}

                                {settings?.showNoteTags && note.Tags && note.Tags.length > 0 && (
                                  <>
                                    {note.Tags.slice(0, 1).map((tag) => (
                                      <span
                                        key={tag.id}
                                        className="px-1.5 py-0.5 rounded-full font-medium cursor-pointer transition-opacity"
                                        style={tag.color ? { backgroundColor: `${tag.color}20`, color: tag.color } : { backgroundColor: 'var(--tag-bg)', color: 'var(--tag-text)' }}
                                        onMouseEnter={(e) => {
                                          if (!tag.color) {
                                            e.currentTarget.style.backgroundColor = 'var(--tag-hover-bg)';
                                          }
                                        }}
                                        onMouseLeave={(e) => {
                                          if (!tag.color) {
                                            e.currentTarget.style.backgroundColor = 'var(--tag-bg)';
                                          }
                                        }}
                                        onClick={(e) => {
                                          e.stopPropagation();
                                          setSelectedTag(tag.name);
                                          setPage(1);
                                        }}
                                      >
                                        {tag.name}
                                      </span>
                                    ))}
                                    {note.Tags.length > 1 && (
                                      <span className="px-1.5 py-0.5 rounded-full" style={{ backgroundColor: 'var(--tag-bg)', color: 'var(--tag-text)' }}>
                                        +{note.Tags.length - 1}
                                      </span>
                                    )}
                                  </>
                                )}

                                {/* 旧格式标签（字符串） */}
                                {settings?.showNoteTags && (!note.Tags || note.Tags.length === 0) && note.tags && (
                                  <>
                                    {note.tags.split(',').slice(0, 1).map((tag, idx) => (
                                      <span
                                        key={idx}
                                        className="px-1.5 py-0.5 rounded-full font-medium cursor-pointer transition-opacity"
                                        style={{ backgroundColor: 'var(--tag-bg)', color: 'var(--tag-text)' }}
                                        onClick={(e) => {
                                          e.stopPropagation();
                                          setSelectedTag(tag.trim());
                                          setPage(1);
                                        }}
                                      >
                                        {tag.trim()}
                                      </span>
                                    ))}
                                    {note.tags.split(',').length > 1 && (
                                      <span className="px-1.5 py-0.5 rounded-full" style={{ backgroundColor: 'var(--tag-bg)', color: 'var(--tag-text)' }}>
                                        +{note.tags.split(',').length - 1}
                                      </span>
                                    )}
                                  </>
                                )}
                              </div>
                            )}
                          </div>
                        </div>
                      </div>
                    )}
                  </motion.div>
                );
              })}
            </Masonry>
          ) : (
            <div className="text-center py-16 text-text-tertiary">
              <p className="text-lg">暂无笔记</p>
            </div>
          )}

          {/* 分页 */}
          {totalPages > 1 && (
            <div className="flex justify-center gap-2 pt-8 mt-8">
              <button
                onClick={() => setPage((p) => Math.max(1, p - 1))}
                disabled={page === 1}
                className="px-4 py-2 rounded-lg bg-bg-secondary text-text-secondary hover:bg-bg-tertiary disabled:opacity-30 disabled:cursor-not-allowed transition-all text-sm border border-border-primary"
              >
                上一页
              </button>

              <div className="flex items-center gap-2">
                {Array.from({ length: Math.min(totalPages, 5) }, (_, i) => {
                  let pageNum;
                  if (totalPages <= 5) {
                    pageNum = i + 1;
                  } else if (page <= 3) {
                    pageNum = i + 1;
                  } else if (page >= totalPages - 2) {
                    pageNum = totalPages - 4 + i;
                  } else {
                    pageNum = page - 2 + i;
                  }

                  const isActive = pageNum === page;
                  const btnClass = 'w-10 h-10 rounded-lg transition-all text-sm border ' + (isActive ? 'bg-primary-500 text-white border-primary-500' : 'bg-bg-secondary text-text-secondary hover:bg-bg-tertiary border-border-primary');

                  return (
                    <button
                      key={pageNum}
                      onClick={() => setPage(pageNum)}
                      className={btnClass}
                    >
                      {pageNum}
                    </button>
                  );
                })}
              </div>

              <button
                onClick={() => setPage((p) => Math.min(totalPages, p + 1))}
                disabled={page === totalPages}
                className="px-4 py-2 rounded-lg bg-bg-secondary text-text-secondary hover:bg-bg-tertiary disabled:opacity-30 disabled:cursor-not-allowed transition-all text-sm border border-border-primary"
              >
                下一页
              </button>
            </div>
          )}
        </div>

        {/* 页脚版权 */}
        <footer className="mt-auto border-t border-border-primary py-6 text-center text-sm text-text-tertiary">
          <p>{settings.footerCopyright}</p>
          <Copyright />
        </footer>
      </div>

    </main>
  );
}
